<template>
  <div class="config-box">
    <br>
    <h2></h2>
    <br>
    <Form :label-width="100" ref="ruleInline">
      <FormItem label="">
        <Input v-model="username"></Input>
      </FormItem>
      <FormItem label="">
        <Input type="password" v-model="password"></Input>
      </FormItem>
      <FormItem label="">
        <Input type="password" v-model="password1"></Input>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="isReady()"></Button>
      </FormItem>
    </Form>
    <Modal v-model="ready" title="" @on-ok="submit()" @on-cancel="cancel()">
      <p></p>
      <p></p>
    </Modal>
  </div>
</template>
<script>
export default {
  data() {
    return {
      username: this.$store.state.username,
      password: null,
      password1: null,
      ready: false,
      ruleInline: {
        password: {
          type: "string",
          min: 6,
          message: "6",
          trigger: "blur"
        }
      }
    };
  },
  methods: {
    submit() {
      var that = this;
      var parms = new URLSearchParams();

      parms.append("username", this.username);
      parms.append("password", this.password);
      parms.append("oldUsername", this.$store.state.username);
      this.$http({
        method: "PUT",
        data: parms,
        url: that.GLOBAL.adress + "/admin/update",
        headers: {
          token: this.$store.state.token
        }
      }).then(function(res) {
        that.configList = res.data.data;
        that.$router.push({ path: "/login" });
      });
    },
    cancel() {},
    isReady() {
      if (
        this.GLOBAL.isEmpty(this.username) ||
        this.GLOBAL.isEmpty(this.password)
      ) {
        this.$Notice.error({
          title: ""
        });
        return;
      }
      if (this.password.length < 6) {
        this.$Notice.error({
          title: ""
        });
        return;
      }
      if (this.password != this.password1) {
        this.$Notice.error({
          title: ""
        });
        return;
      }
      this.ready = true;
    }
  }
};
</script>
<style>
.config-box {
  text-align: center;
  margin: 0 auto;
  width: 400px;
}
</style>
